.backdrop.backdrop-team-modal {
  padding: 30px 0;
  @media screen and (min-width: $tablet) {
    padding-top: 40px;
  }
}
.team-modal {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 50%;
  height: 100%;
  border-radius: 100px;
  background: white;
}
.team-modal-close-button {
  right: 5%;
  width: 30px;
  height: 30px;
  border: 1px solid;
  border-color: #ff6b08;
  border-radius: 50px;
  background-color: #ff6b08;
  transition: 1s;
  transform: scale(1);
  z-index: 100;
  @media screen and (max-width: 480px) {
    top: 3%;
    right: 3%;
  }
}
.team-modal-close-button:hover {
  border-color: #ff6b08;
  background-color: #ff001b;
  cursor: pointer;
}
.close-icon {
  width: 100%;
  height: 100%;
  fill: #ffffff;
  transition: 1s;
  transform: scale(1);
}
.team-modal-close-button:hover .close-icon {
  fill: #ff6b08;
  border-color: #ff6b08;
}
.team-card-list {
  justify-content: center;
  padding: 30px;
  &-item {
    background-image: url(../images/kino-fon.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 25px;
    box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 1px 3px rgba(0, 0, 0, 0.12);
    height: 100%;
    padding-bottom: 30px;
    transition: 0.8s;

    &:hover {
      box-shadow: 0 0 20px rgba(250, 119, 12, 0.897);
      transform: scale(1.03);
    }
  }
  &-img {
    margin: 0 auto;
    margin-bottom: 20px;
    border-radius: 50%;
  }
}
.team-discription {
  font-family: Roboto;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 15px;
  color: white;
}
.team-contact {
  display: block;
  font-family: Roboto;
  font-size: 10px;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
}

@media screen and (max-width: 767px) {
  .team-card-list {
    display: block;
    &-item {
      margin: 0 auto;
      max-width: 320px;

      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }
  .team-name {
    font-family: Roboto;
    font-size: 26px;
    font-weight: 500;
    line-height: 1.17;
    margin-bottom: 20px;
    text-transform: uppercase;
    text-align: center;
    color: black;
  }
  .team-contact {
    font-size: 16px;
  }
}
@media screen and (min-width: 768px) {
  .team-card-list {
    display: flex;
    flex-wrap: wrap;
    &-item {
      max-width: calc((100% - 40px) / 2);
    }
  }
  .team-name {
    font-family: Roboto;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.17;
    margin-bottom: 20px;
    text-transform: uppercase;
    text-align: center;
    color: white;
  }
  .team-contact {
    font-size: 16px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
  .team-card-list-item {
    margin-bottom: 30px;
    &:nth-child(odd) {
      margin-right: 30px;
    }
  }
}
@media screen and (min-width: 1024px) {
  .team-card-list {
    flex-wrap: wrap;
    &-item {
      max-width: calc((100% - 30px * 4) / 6);
      margin-bottom: 50px;
      margin-right: 30px;
      &:last-child {
        margin-right: 0px;
      }
      &:nth-child(5) {
        margin-right: 0px;
      }
    }
  }
  .team-name {
    font-size: 16px;
  }
  .team-discription {
    font-size: 16px;
  }
  .team-contact {
    font-size: 10px;
  }
}
