.titleText {
  font-weight: 900;
  font-size: 14px;
  position: absolute;
  width: 90px;
  top: 30px;
  right: 0;
  color: $mark-color;
  text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff,
    -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
  text-transform: uppercase;
  cursor: pointer;
  transition: color 300ms ease, text-shadow 300ms ease;

  &:hover,
  &:focus {
    color: #ff4800;
    text-shadow: 2px 0 10px #fff, -2px 0 10px #fff, 0 2px 10px #fff, 0 -2px 10px #fff, 1px 1px #fff,
      -1px -1px 10px #fff, 1px -1px 10px #fff, -1px 1px 10px #fff;
    text-transform: uppercase;
  }
}

.fa {
  font-size: 45px;
}

.cat-modal {
  position: fixed;
  opacity: 1;
  transform: translateX(0%);
  transition: opacity 1000ms ease-out, transform 500ms ease;
  bottom: 0;
  display: block;
  box-sizing: 0;
  right: 0;

  width: 280px;
  height: 132px;

  border-top-right-radius: 15px;
  border-top-left-radius: 24px;
  border-bottom-right-radius: 0;
  box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.5);
  border: 2px solid rgba(96, 87, 87, 0.5);

  @media screen and (min-width: $tablet) {
    right: 0px;
    transform: translateX(0%);
  }
}

.cat-modal.cat-hidden .cat-modal {
  opacity: 0.1;
}

.catColor {
  /*This universally changes the colour of the cat. For multi-coloured cats, delete it and apply background-color to components individually*/
  background-color: #242424;
}

.catContainer {
  position: relative;
  top: -160px;
  width: 50%;
  height: 500px;
  left: -50%;
  transform: translateX(-600px);
}

.catHead {
  width: 170px;
  height: 130px;
  border-radius: 50%;
  -webkit-border-radius: 55% 55% 50% 50%;
  margin-left: 718px;
  margin-top: 200px;
  position: absolute;
  z-index: 2;
  box-shadow: 0px 4px 8px -6px #111;
}

.catHair {
  width: 70px;
  height: 70px;
  position: relative;
  border: none;
  position: absolute;
  margin-top: 208px;
  margin-left: 772px;
}

.catHair::before {
  width: 80px;
  height: 70px;
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  border: none;
  background: inherit;
  -webkit-transform: rotateZ(26deg);
  transform: rotateZ(26deg);
}

.catHair::after {
  width: 72px;
  height: 70px;
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  border: none;
  background: inherit;
  -webkit-transform: rotateZ(60deg);
  transform: rotateZ(60deg);
}

.catEar {
  width: 80px;
  height: 100px;
  border: none;
  -webkit-border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  position: absolute;
  z-index: 1;
}

.catLeftEar {
  -webkit-transform: rotateZ(-45deg);
  transform: rotateZ(-45deg);
  margin-left: 717px;
  margin-top: 173px;
}

.catRightEar {
  -webkit-transform: rotateZ(-20deg);
  transform: rotateZ(20deg);
  margin-left: 820px;
  margin-top: 175px;
}

.catInsideEar {
  width: 60px;
  height: 100px;
  border: 1px;
  -webkit-border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  border-radius: 80% 0 55% 50% / 55% 0 80% 50%;
  background: $mark-color;
  z-index: 1;
}

.catLeftInsideEar {
  margin-left: 12px;
  margin-top: 6px;
}

.catRightInsideEar {
  margin-left: 10px;
  margin-top: 6px;
}

.catEye {
  width: 62px;
  height: 50px;
  -webkit-border-radius: 50%;
  border-radius: 60% 60% 60% 60%;
  background-color: #e6e6e6;
  z-index: 3;
  position: absolute;
  margin-top: 32px;
}

.catLeftEye {
  margin-left: 17px;
}

.catRightEye {
  margin-left: 93px;
}

.eyelid {
  height: 60%;
  width: 100%;
  position: absolute;
  z-index: 10;
  margin-top: -5%;
  border-radius: 100% 100% 10% 10%;
  animation: blink 6s infinite ease-in-out;
  animation-delay: 7s;
}

@keyframes blink {
  1% {
    height: 60%;
  }
  2% {
    height: 80%;
  }
  3% {
    height: 100%;
  }
  4% {
    height: 80%;
  }
  5% {
    height: 60%;
  }
  100% {
    height: 60%;
  }
}

.pupil {
  width: 20px;
  height: 35px;
  background-color: #222;
  -webkit-border-raidus: 50%;
  border-radius: 50%;
  margin-left: 20px;
  z-index: 5;
  margin-top: 3px;
}

.catBody {
  width: 185px;
  height: 110px;
  border: none;
  -webkit-border-radius: 50% / 60% 60% 35% 35%;
  border-radius: 50% / 80% 80% 50% 50%;
  position: absolute;
  margin-left: 712px;
  margin-top: 276px;
  z-index: 1;
}

.catFoot {
  height: 28%;
  width: 30%;
  position: absolute;
  z-index: 10;
  margin-top: 43%;
  border-radius: 100% 100% 70% 70%;
  -webkit-box-shadow: -1px 2px 20px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 2px 20px -5px rgba(0, 0, 0, 0.75);
  box-shadow: -2px -4px 10px -6px rgba(0, 0, 0, 1);
}

.leftFoot {
  margin-left: 8%;
  animation: step 9s infinite ease-in-out;
  animation-delay: 7.4s;
}

.rightFoot {
  margin-left: 60%;
  animation: step 9s infinite ease-in-out;
  animation-delay: 8s;
}

@keyframes step {
  1% {
    transform: translateY(-1px);
  }
  2% {
    transform: translateY(-2px);
  }
  3% {
    transform: translateY(-3px);
  }
  4% {
    transform: translateY(-2px);
  }
  5% {
    transform: translateY(-1px);
  }
  6% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(0px);
  }
}

.catTail {
  position: absolute;
  margin-left: 870px;
  margin-top: 352px;
  translate: rotate(-10deg);
}

// /*Each span creates a circle that extends the tail and animates up and down individually, add more and style with left: +5px and a 0.2s additional delay from the previous to create a longer tail.*/

.catContainer span {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 50px;
  animation: tailWave 3s infinite ease-in-out;
}

.catContainer span:nth-child(2) {
  left: 5px;
  animation-delay: 0.2s;
}

.catContainer span:nth-child(3) {
  left: 10px;
  animation-delay: 0.4s;
}

.catContainer span:nth-child(4) {
  left: 15px;
  animation-delay: 0.6s;
}

.catContainer span:nth-child(5) {
  left: 20px;
  animation-delay: 0.8s;
}

.catContainer span:nth-child(6) {
  left: 25px;
  animation-delay: 1s;
}

.catContainer span:nth-child(7) {
  left: 30px;
  animation-delay: 1.2s;
}

.catContainer span:nth-child(8) {
  left: 35px;
  animation-delay: 1.4s;
}

.catContainer span:nth-child(9) {
  left: 40px;
  animation-delay: 1.6s;
}

.catContainer span:nth-child(10) {
  left: 45px;
  animation-delay: 1.8s;
}

span:nth-child(11) {
  left: 50px;
  animation-delay: 2s;
}

.catContainer span:nth-child(12) {
  left: 55px;
  animation-delay: 2.2s;
}

.catContainer span:nth-child(13) {
  left: 60px;
  animation-delay: 2.4s;
}

.catContainer span:nth-child(14) {
  left: 65px;
  animation-delay: 2.6s;
}

.catContainer span:nth-child(15) {
  left: 70px;
  animation-delay: 2.8s;
}

@keyframes tailWave {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-6px);
  }
  100% {
    transform: translateY(0px);
  }
}

.cat-hidden {
  opacity: 0;
  pointer-events: none;
  transform: translateX(110%);
}

.close-annoyment {
  position: absolute;
  top: -3px;
  right: -3px;

  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  &:hover .icon-cat-close,
  &:focus .icon-cat-close {
    transform: scale(1.1);
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  }
}

.icon-cat-close {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background-color: $mark-color;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  transition: box-shadow 300ms ease, transform 300ms ease;
}
